<template>
  <div class="playMusicComponent" v-if="item" v-show="this.$store.state.IscomponentShow">
    <div class="image" @click="getplaysong">
      <!-- <img :src="item.song.album.picUrl" v-if="item.song" />
      <img :src="item.al.picUrl" v-if="item.al" />
      <img src="../image/logo.jpg" v-if="imgshow" class="imgs" />-->
      <div v-if="item.song">
        <van-image :src="item.song.album.picUrl">
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
        </van-image>
      </div>
      <div v-if="item.al">
        <van-image :src="item.al.picUrl">
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
        </van-image>
      </div>
    </div>
    <div class="playAudio" v-if="itemrul.url">
      <audio :src="itemrul.url" autoplay="autoplay" controls="controls" @ended="turnnext"></audio>

      <div class="detail">
        <span class="setlong">
          <span>{{item.name}}</span>
          <!-- <span v-if="item.song">{{item.song.artists[0].name}}</span>
          <span v-if="item.ar">{{item.ar[0].name}}</span>-->
          <p>横划切换上下首</p>
        </span>
        <div class="setshort">
          <i class="el-icon-video-play playmusic" @click="playmusic"></i>
          <i class="el-icon-s-fold foldmusic" @click="showfoldmusic"></i>
        </div>
      </div>

      <!-- <i class="el-icon-caret-left turnleft" @click="turnlast"></i>
      <i class="el-icon-caret-right turnright"  @click="turnnext"></i>-->
    </div>
    <div class="musiclist" v-if="showlist">
      <span class="radow" @click="setradow">随机播放</span>
      <i class="el-icon-s-unfold unfoldmusic" @click="showfoldmusic2"></i>
      <div class="setover">
        <ul>
          <li v-for="(item, index) in musiclist" :key="index" @click="playsong(item,musiclist)">
            <p>
              <span v-if="item.song">{{item.song.artists[0].name}}-</span>
              <span v-if="item.ar">{{item.ar[0].name}}-</span>
              {{item.name}}
            </p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
@keyframes imgrotate {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.playMusicComponent {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: white;
  padding-top: 5px;
  .image {
    width: 20%;
    // height: 60px;
    float: left;

    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    .van-image {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      animation: imgrotate 3s linear infinite;
      overflow: hidden;
    }
    // .imgs {
    //   position: absolute;
    //   top: 0;
    //   left: 17px;
    // }
  }
  .playAudio {
    width: 80%;
    float: right;
    //  position: relative;
    audio {
      width: 100%;
      display: none;
    }
    .detail {
      //   .turnleft{
      //     float: left;
      // }
      // .turnright{
      //     float: right;
      // }
      .setlong {
        width: 70%;
        float: left;

        font-size: 10px;
        p {
          margin: 0;
          padding: 0;
        }
        span {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          word-break: normal;
          width: 150px;
          display: block;
        }
      }
      .setshort {
        width: 30%;
        float: left;
        text-align: center;
        i {
          font-size: 25px;
          line-height: 31px;
          padding: 0 5px;
        }
      }
    }
  }
  .musiclist {
    width: 100%;
    height: 250px;
    position: absolute;
    bottom: 0;
    left: 0;

    background: white;
    overflow: scroll;
    border-top: 1px solid gray;
    color: rgb(20, 20, 20);
    .radow {
      float: left;
      line-height: 40px;
      font-size: 14px;
      padding-left: 10px;
    }
    .unfoldmusic {
      font-size: 25px;
      line-height: 40px;
      padding-left: 220px;
    }
    .setover {
      clear: both;
      border-top: 1px solid gray;
      ul {
        list-style: none;
        margin: 0;
        padding: 0;
        li {
          // border-bottom: 1px solid gray;

          p {
            line-height: 35px;
            font-size: 14px;
            margin: 0;
            padding-left: 20px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            word-break: normal;
          }
          span {
            font-size: 10px;
            color: grey;
          }
        }
      }
    }
  }
}
</style>

<script>
export default {
  data() {
    return {
      Mp3url: null,
      showlist: false
    };
  },

  methods: {
    turnlast: function() {
      this.$store.commit("TurnlastSong");
    },
    turnnext: function() {
      this.$store.commit("TurnnextSong");
    },
    playmusic: function() {
      var audio = document.querySelector(".playAudio audio");
      // if (audio.paused) {
      // audio.play();
      this.$store.commit("SetisplayNot");
      // }else {
      // audio.pause();
      //  this.$store.commit('SetisplayNot');
      // }
      // var playmusic=document.querySelector(".playAudio .playmusic");
      // if (playmusic.className.includes("el-icon-video-pause")) {
      //   playmusic.className="el-icon-video-play playmusic"
      // }else{
      //   playmusic.className="el-icon-video-pause playmusic"
      // }
    },
    showfoldmusic: function() {
      this.showlist = true;
    },
    showfoldmusic2: function() {
      if (this.showlist) {
        this.showlist = false;
      }
    },
    setradow() {
      console.log("setradow");
    },
    playsong(item, list) {
      // console.log(item.id);
      let that = this;

      axios
        .get("/song/url", {
          params: {
            id: item.id
          }
        })
        .then(function(response) {
          // console.log(response);
          that.$store.commit("Setcurrentmusicurl", response.data.data[0]);
          that.$store.commit("Setmusicid", item.id);
          that.$store.commit("SetcurrentMusic", item);
          that.$store.commit("SetcurrentMusiclist", list);
          that.$store.commit("SetNotChageMusicList", list);
          // console.log(that.personalizedList);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getplaysong() {
      this.$router.push({ name: "playsong" });
    }
  },
  computed: {
    itemrul: function() {
      if (this.$store.state.musicurl) {
        return this.$store.state.musicurl;
      }
    },
    musiclist: function() {
      return this.$store.state.NotChageMusicList;
    },
    item: function() {
      return this.$store.state.currentMusic;
    },
    // imgshow: function() {
    //   return this.$store.state.imgshow;
    // },
    isplays: function() {
      return this.$store.state.isplay;
    }
  },
  created() {
    // setTimeout(() => {
    //   this.$store.commit("SetimgshowFalse");
    // }, 2000);
  },
  watch: {
    item: function() {
      // this.$store.commit("Settime",time);
      // console.log(this.$store.state.time);
    },
    isplays: function() {
      var audio = document.querySelector(".playAudio audio");
      if (this.isplays) {
        audio.play();
      } else {
        audio.pause();
      }
      var playmusic = document.querySelector(".playAudio .playmusic");
      if (playmusic.className.includes("el-icon-video-pause")) {
        playmusic.className = "el-icon-video-play playmusic";
      } else {
        playmusic.className = "el-icon-video-pause playmusic";
      }
      console.log("chage");
    }
  }
};
</script>
