import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      children:[
        {
          path: '/recommendMusic',
          name: 'recommendMusic',
          component: () => import('./views/RecommendMusic.vue')
        },
        {
          path: '/hotSongList',
          name: 'hotSongList',
          component: () => import( './views/HotSongList.vue')
        },
        {
          path: '/search',
          name: 'search',
          component: () => import('./views/Search.vue')
        },
      ]
    },
    {
      path: '/about',
      name: 'about',
      component: () => import( './views/About.vue')
    },
   
    {
      path: '/songDetail',
      name: 'songDetail',
      component: () => import('./views/SongDetail.vue')
    },
    {
      path: '/playsong',
      name: 'playsong',
      component: () => import( './views/PlaySong.vue')
    },
  ]
})
