import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    musicid: null,
    fullscreenLoading: false,
    currentMusic: null,
    musicurl: null,
    Musiclist: null,
    nowNumber: null,
    IscomponentShow:true,
    // imgshow:true,
    NotChageMusicList:null,
    isplay:true,
    nowtime:null,
    time:null
  },
  mutations: {
    Setnowtime(state, nowtime) {
      state.nowtime = nowtime;
    },
    Settime(state, time) {
      state.time = time;
    },
    SetisplayNot(state) {
      state.isplay = !state.isplay;
    },
    SetfullscreenLoadingFalse(state) {
      state.fullscreenLoading = false;
    },
    SetfullscreenLoadingTrue(state) {
      state.fullscreenLoading = true;
    },
    Setmusicid(state, id) {
      state.musicid = id;
    },
    SetcurrentMusic(state, currentMusic) {
      state.currentMusic = currentMusic;
    },
    SetNotChageMusicList(state, NotChageMusicList) {
      state.NotChageMusicList = NotChageMusicList;
    },
    Setcurrentmusicurl(state, musicurl) {
      state.musicurl = musicurl;
    },
    SetcurrentMusiclist(state, Musiclist) {
      state.Musiclist = Musiclist;
    },
    TurnnextSong(state) {

      // eslint-disable-next-line no-empty


      for (let index = 0; index < state.Musiclist.length; index++) {
        if (state.Musiclist[index].id == state.musicid) {
          if (index == state.Musiclist.length - 1) {
            state.nowNumber = 0;
          } else {
            state.nowNumber = index + 1;
          }

        }
      }
      state.musicid = state.Musiclist[state.nowNumber].id;

      state.currentMusic = state.Musiclist[state.nowNumber];
      // eslint-disable-next-line no-undef
      axios
        .get("/song/url", {
          params: {
            id: state.musicid
          }
        })
        .then(function (response) {
          state.musicurl = response.data.data[0];
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    },
    TurnlastSong(state) {
      for (let index = 0; index < state.Musiclist.length; index++) {
        if (state.Musiclist[index].id == state.musicid) {
          if (index == 0) {
            state.nowNumber = state.Musiclist.length - 1;
          } else {
            state.nowNumber = index - 1;
          }

        }
      }
      state.musicid = state.Musiclist[state.nowNumber].id;

      state.currentMusic = state.Musiclist[state.nowNumber];
      // eslint-disable-next-line no-undef
      axios
        .get("/song/url", {
          params: {
            id: state.musicid
          }
        })
        .then(function (response) {
          state.musicurl = response.data.data[0];
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    },
    SetIscomponentShowTrue(state) {
      state.IscomponentShow = true;
    },
    SetIscomponentShowFalse(state) {
      state.IscomponentShow = false;
    },
    // SetimgshowTrue(state) {
    //   state.imgshow = true;
    // },
    // SetimgshowFalse(state) {
    //   state.imgshow = false;
    // },
  },
  actions: {

  }
})
